  tab-size: 0;
} */

.App {
  width: 100%;
  height: 100%;
  text-align: center;
  /* box-sizing: initial; */
  font-family: 'Quicksand', sans-serif;
  background-color: white;
  display: grid;
  grid-gap: 1vw 0;
  /* grid-template-columns: auto auto auto; */
  /* grid-template-rows: 1fr auto 5fr; */
  grid-row-gap: 1rem;
  grid-template-areas:
    "header"
    "main"
    "main2"
    "footer";
}

h1, th {
  color: #0d5;
  font-weight: 800;
  margin: 0px;
}

h2, td {
  color: grey;
  font-weight: 500;
  margin: 0px;
}

.App-header {
  grid-area: header;
  top: 0px;
  display: block;
}

.App-footer {
  grid-area: footer;
  display: block;
  position: fixed;
  background-color: white;
  bottom: 0px;
  width: 100vw;
}

.App-social {
  position: relative;
  display: flex;
  justify-content: center;
  /* line-height: 0; */
  /* width: 200px; */
  /* height: 120px; */

}

.socialBtns {
  padding: 1rem;
}

.socialBtns:hover{
  filter: drop-shadow(12px 12px 7px rgba(0, 221, 85, 0.4));
}

.App-bio {
  display: grid;
  position: relative;
  grid-area: main;
  color: black;
  /* grid-gap: 20px 0; */
  grid-template-columns: 2fr 4fr 4fr;
  /* grid-auto-rows: auto; */
}

.profilePic{

  height: 20vw;
  margin: 1rem;
}

.myBio{
  padding: 1rem;
  /* font-size: 4rem; */
  text-align: left;

}

.experience{
  text-align: start;
  /* font-size: 4rem; */
  /* width: 40%; */
}

th, tr {
  padding-bottom: 1rem;  
}

td {
  padding: 1fr 0;
  /* padding-right: 30%; */
}

table {
  table-layout: fixed;
  width: 100%;
  /* font-size: 4rem; */
  border-collapse: separate;
  /* border-spacing: 10px 0; */

}

tr:nth-child(even) {
  background: linear-gradient(-85deg, white, #0d5);
}

.myApps {
  position: relative;
  grid-area: main2;
  background: linear-gradient(85deg,#0d5, white);
}

.appImages {
  display: flex;
  justify-content: space-evenly;
}

.appImages img{
  height: 60%;

}

@media screen and (max-width : 600px){

  .App{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    max-width: 95%;
  }

  .App-header{
    font-size: 1rem;
    letter-spacing: 0;
    display: block;
    margin-bottom: 20px;
  }

  .App-bio{
    display: flex;
    flex-direction: column;
  }

  table, .experience{
    width: 100%;
    align-items: center;
  }

  .myBio{
    font-size: 1rem;
    margin: 0 5%;
  }

  table{
    font-size: 1rem;
    margin: 20px 0;
    border-spacing: .5rem 0;
  }

  img.socialBtns, .profilePic{
    width: 50%;
    height: auto;
    padding: 0;
    margin: 0;
    align-self: center;
  }

  .myApps h2{
    display: block;
    font-size: 100%;
  }

  .appImages{
    display: grid;
    grid-template-columns: auto;
    justify-content: space-evenly;
    text-align: center;
  }

}
